import {profileAPI, usersAPI} from "../api/api";
import {stopSubmit} from "redux-form";

    const ADD_POST = 'my-app/profile/ADD-POST';
    const SET_USER_PROFILE = 'my-app/profile/SET_USER_PROFILE';
    const SET_STATUS = 'my-app/profile/SET_STATUS';
    const DELETE_POST = 'my-app/profile/DELETE_POST';
    const SAVE_PHOTO_SUCCESS = 'my-app/profile/SAVE_PHOTO_SUCCESS';

    let initialState = {
        posts: [
            {id: 1, message: ' ЗААААА', likeCounts: 25},
            {id: 2, message: ' sdsss', likeCounts: 12}
        ],
        profile : null,
        status: '',
    };

    const profileReducer = (state = initialState,action) =>{

        switch (action.type){
            case ADD_POST:
                let newPost = {
                    id: 5,
                    message: action.newPostText,
                    likeCounts: 0
                }
                return  {
                    ...state,
                    posts: [...state.posts, newPost],
                    newPostText: ''
                };

            case SET_USER_PROFILE:
                return {
                    ...state,
                    profile: action.profile
                }
            case SET_STATUS :
                return {
                    ...state,
                    status: action.status
                }
            case DELETE_POST:
                return {
                    ...state,
                    posts: state.posts.filter(p => p.id !== action.postId)
                }
            case SAVE_PHOTO_SUCCESS:
                return {
                    ...state,
                   profile: {...state.profile,photos: action.photos}
                }
            default:
                return state;
        }
    }

    export const addPost = (newPostText) =>({type: ADD_POST,newPostText});
    export const deletePost = (postId) => ({type: DELETE_POST, postId});
    export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile});
    export const setStatus = (status) => ({type: SET_STATUS, status});
    export const savePhotoSuccess = (photos) =>({type:SAVE_PHOTO_SUCCESS, photos});


    export const getUserProfile = (userId) =>
        async (dispatch) => {
            let response = await usersAPI.getProfile(userId)
            dispatch(setUserProfile(response.data));
        }
    export const getUserStatus = (userId) =>
        async (dispatch) => {
            let response = await profileAPI.getStatus(userId)
                    dispatch(setStatus(response.data));
        }

    export const updateUserStatus = (status) =>
        async (dispatch) => {
            try {
                let response = await profileAPI.updateStatus(status)
                if (response.data.resultCode === 0) {
                    dispatch(setStatus(status));
                }
            } catch (e) {
                console.log('error');
            }
        }
   export const savePhoto = (file) =>
       async (dispatch) => {
       let response = await profileAPI.savePhoto(file);
       if(response.data.resultCode === 0){
           dispatch(savePhotoSuccess(response.data.data.photos));
       }
   }

    export const saveProfile = (profile) =>
        async (dispatch, getState) => {
        const userId = getState().auth.userId;
            let response = await profileAPI.saveProfile(profile);
            if(response.data.resultCode === 0){
                dispatch(getUserProfile(userId))
            } else {
                dispatch(stopSubmit('edit-profile',{_error: response.data.messages[0]}));
                return Promise.reject(response.data.messages[0])
            }
        }



export default profileReducer;